@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@200;300;400;500;700&display=swap);
.login-container {
  align-items: center;
  display: flex;
  flex-direction: column;
  height: 100vh;
  justify-content: center;
}

.login-form {
  background-color: white;
  display: flex;
  flex-direction: column;
  padding: 80px;
  width: 40%;
}

.login-form h1 {
  text-align: center;
}

.login-form label {
  margin-top: 24px;
}

.login-form input {
  border-bottom: 2px solid var(--secondary-background-color);
  padding: 8px;
  width: 100%;
}

.login-form button {
  background-color: var(--yellow);
  margin-top: 24px;
  padding: 16px;
}

.wallet-container header {
  align-items: center;
  background-color: var(--secondary-background-color);
  color: white;
  display: flex;
  justify-content: space-between;
  padding: 32px;
}

.wallet-container header h1 {
  font-weight: 300;
}

.wallet-container header div {
  display: flex;
}

.wallet-container header div p {
  font-weight: 300;
  margin-left: 16px;
}

.wallet-form {
  align-items: center;
  background-color: black;
  color: white;
  display: flex;
  justify-content: center;
  padding: 32px;
}

.wallet-form label {
  margin-right: 16px;
}

.wallet-form input , .wallet-form select {
  padding: 4px;
}

.wallet-form button , .wallet-table button {
  background-color: var(--yellow);
  padding: 4px 8px;
}

.wallet-table {
  text-align: center;
  width: 100%;
}

.wallet-table thead {
  background-color: white;
}

.wallet-table th , .wallet-table td {
  padding: 16px;
}

.wallet-table td {
  border-bottom: 1px solid white;
}


:root {

  --background-color: rgb(240, 240, 240);

  --secondary-background-color: rgb(34, 34, 34);

  --yellow: rgb(251, 192, 4);
}

* {
  border: 0;
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  background-color: rgb(240, 240, 240);
  background-color: var(--background-color);
  font-family: Montserrat, sans-serif;
  font-weight: 400;
}

select , input , label , button {
  font-family: Montserrat, sans-serif;
}

button {
  cursor: pointer;
}

